import { memo, useEffect } from "react"
import { actived, base_url, el } from "../Function/Global"
import Icons from "../Components/Icons"
import NavigationList from "../Components/NavigationList"

const Sidebar = () => {
    const navs = [
        {
            to: base_url("dashboard"),
            name: "Dashboard",
            icon: <Icons iconName="dashboard" />
        },
        {
            to: base_url("icon"),
            name: "Icon",
            icon: <Icons iconName="rocket-launch" />
        },
        {
            to: base_url("category"),
            name: "Category",
            icon: <Icons iconName="document-signed" />
        },
        {
            to: base_url("type"),
            name: "Type",
            icon: <Icons iconName="list" />
        },
        {
            to: base_url("dimension"),
            name: "Dimension",
            icon: <Icons iconName="size" />
        },
        {
            to: base_url("user"),
            name: "Users",
            icon: <Icons iconName="users" />
        }
    ]

    const userAccountList = [
        {
            to: base_url("profile"),
            name: "Profile",
            icon: <Icons iconName="user" />
        },
        {
            to: base_url("setting"),
            name: "Setting",
            icon: <Icons iconName="settings" />
        }
    ]

    const sidebarCollapse = () => {
        el(".admin").classList.toggle("sidebar-collapse")

        const sidebar = el(".sidebar")
        const isCollapse = sidebar.classList.contains("collapse")
        isCollapse ?
            setTimeout(() => sidebar.classList.remove("collapse"), 200) :
            sidebar.classList.add("collapse")
    }

    // TODO: Active link saat pertama kali di load
    useEffect(() => { actived() }, [])

    return (
        <nav className="sidebar" onClick={actived}>
            <div className="logo">
                <img src={base_url("assets/geticon.webp")} alt="Get Icon" />
                <h4>Get Icon</h4>
            </div>

            <NavigationList list={navs} />
            <NavigationList list={userAccountList} />

            <div className="collapse-sidebar" onClick={sidebarCollapse}>
                <Icons iconName="angle-right" />
            </div>
        </nav >
    )
}

export default memo(Sidebar)