import { memo } from "react"
import { useNavigate } from "react-router-dom"
import { base_url, showPassword } from "../../Function/Global"
import { setAuth } from "../../Function/Auth"
import Input from "../../Components/Input"
import Icons from "../../Components/Icons"
import { Toast } from "../../Function/Index"

const Signin = ({ api }) => {
    const navigate = useNavigate()

    const formAttributes = [
        { type: "email", placeholder: "Enter your email address" },
        { type: "password", placeholder: "Enter your password", children: <Icons onClick={showPassword} iconName="eye-crossed" />, classList: "_input-password" },
        { type: "checkbox", name: "remember-me", classList: "remember-me", children: <label>Recovery Password</label> }
    ]

    const login = ({ target }) => {
        target.setAttribute("disabled", "")
        const isRemember = target.form.querySelector(`[name="remember-me"]`).checked

        api.post("token", target.form, res => {
            if (!res.ok) {
                Toast.fire({
                    icon: "error",
                    title: "Incoret Email or Password"
                })
            }
            if (res.ok) {
                setAuth(res.data, isRemember)
                return navigate(base_url("dashboard"))
            }

            target.removeAttribute("disabled")
        })
    }

    return (
        <>
            {formAttributes.map(({ type, name, placeholder, children, classList }, i) => (
                <Input
                    key={i}
                    type={type}
                    name={name}
                    placeholder={placeholder}
                    classList={classList}
                >{children}</Input>
            ))}
            <div>
                <button className="button" onClick={login} type="button">Sign In</button>
            </div>
        </>
    )
}

export default memo(Signin)