import { useContext, useEffect, useState } from "react"
import { Api } from "../../Layouts/Template"
import { updateToken } from "../../Function/Auth"
import FetchApi from "../../Api/FetchApi"
import Index from "../../Function/Index"

const Profile = () => {
    let api = useContext(Api)
    const act = new Index({ api, endpoint: "profile" })
    const [user, setUser] = useState()

    useEffect(() => { act.get(res => setUser(res.data)) }, [])

    const updateProfile = ({ target }) => {
        act.update({ target }, res => {
            if (res.ok) {
                updateToken(res.data.regenerate_token)
                setUser(res.data.user)
                api = FetchApi("https://get.icon.giftin-id.com/api", { token: res.data.regenerate_token }) // jangan gunakan garis miring "/" du akhir
            }
        })
    }

    const inputs = [
        { type: "text", name: "name", value: user && user.name, label: "Name" },
        { type: "email", name: "email", value: user && user.email, label: "Email" },
        { type: "password", name: "password", value: "", label: "Password" }
    ]

    return (
        <div className="content">
            <h4 className="container-title">Update Profile</h4>

            <div className="container">
                <form className="form-crud content">
                    {inputs.map(({ type, name, value, label }, i) => (
                        <div key={i}>
                            <input type={type} name={name} defaultValue={value} className="input bg-grey" />
                            <p className="invalid">Error</p>
                            <label>{label} {name !== "password" ? <sup>*</sup> : ""}</label>
                        </div>
                    ))}
                    <button className="button" type="button" onClick={updateProfile}>Update Profile</button>
                </form>
            </div>
        </div>
    )
}

export default Profile