import { useContext, useEffect, useState } from "react"
import { Api } from "../../Layouts/Template"
import { getLargestId } from "../../Function/Global"
import IndexAction from "../../Function/Index"
import Icons from "../../Components/Icons"
import Index from "../../Components/Index"
import Table from "../../Components/Table"

const Dimension = () => {
    const api = useContext(Api)
    const [dimensions, setDimensions] = useState()
    const act = new IndexAction({ api, endpoint: "dimension" })
    const thead = ["No", "Dimension", "Actions"]

    useEffect(() => { act.get(res => setDimensions(res.data)) }, [])
    const searchDimension = async evt => {
        const res = await act.search(evt, dimensions.length)
        if (res) setDimensions(res.data)
    }

    const getNextDimension = ({ target }) => {
        if (!dimensions) return
        act.next({ target, lastId: getLargestId(dimensions) }, res => {
            setDimensions(dimensions => dimensions.concat(res.data))
        })
    }

    const deleteDimension = id => {
        act.delete({
            id,
            take: dimensions.length,
            errorMessage: "Failed to delete, maybe the dimension is being used"
        }, res => setDimensions(res.data))
    }

    return (
        <div className="content" onScroll={getNextDimension}>
            <Index searchOnInput={searchDimension} label="Dimension">
                <Table thead={thead} classList={{ 2: "text-center" }}>
                    {dimensions && dimensions.map(({ dimension, id }, i) => (
                        <tr key={id}>
                            <td>{++i}</td>
                            <td>{dimension}</td>
                            <td className="text-center">
                                <Icons iconName="trash" onClick={() => deleteDimension(id)} />
                            </td>
                        </tr>
                    ))}
                </Table>
            </Index>
        </div>
    )
}

export default Dimension