import { memo } from "react"

const ContainerTitle = ({ labels }) => {
    return (
        <div className="container-title">
            {
                labels.map(({ isActive, content }, i) => (
                    <h4 key={i} className={isActive ? "active" : undefined}>
                        {content}
                    </h4>
                ))
            }
        </div>
    )
}

export default memo(ContainerTitle)