import { createBrowserRouter, RouterProvider } from "react-router-dom"
import { Template, Notfound } from "./Layouts/Layouts"
import { base_url } from "./Function/Global"
import React from "react"
import ReactDOM from "react-dom/client"

import Admin from "./Middleware/Admin"
import Signin from "./Middleware/Signin"

import * as Pages from "./Pages/Pages"
import "./index.css"

const router = createBrowserRouter([
  {
    path: base_url(),
    element: <Pages.SigninSignup />,
    errorElement: <Notfound />,
    loader: Signin
  },
  {
    element: <Template />,
    children: [
      {
        path: base_url("dashboard"),
        element: <Pages.Dashboard />
      },
      {
        path: base_url("icon"),
        children: [
          {
            path: "",
            element: <Pages.Icon />
          },
          {
            path: "create",
            element: <Pages.CreateIcon />
          },
          {
            path: ":className",
            element: <Pages.DetailIcon />
          },
          {
            path: ":className/edit",
            element: <Pages.UpdateIcon />
          }
        ]
      },
      {
        path: base_url("category"),
        children: [
          {
            path: "",
            element: <Pages.Category />
          },
          {
            path: "create",
            element: <Pages.CreateCategory />
          }
        ]
      },
      {
        path: base_url("type"),
        children: [
          {
            path: "",
            element: <Pages.Type />
          },
          {
            path: "create",
            element: <Pages.CreateType />
          }
        ]
      },
      {
        path: base_url("dimension"),
        children: [
          {
            path: "",
            element: <Pages.Dimension />
          },
          {
            path: "create",
            element: <Pages.CreateDimension />
          }
        ]
      },
      {
        path: base_url("user"),
        element: <Pages.User />
      },
      {
        path: base_url("profile"),
        element: <Pages.Profile />
      },
      {
        path: base_url("setting"),
        element: <Pages.Setting />
      }
    ],
    errorElement: <Notfound />,
    loader: Admin
  }
])

const root = ReactDOM.createRoot(document.getElementById("root"))
root.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
)
