import { useContext, useState, useEffect } from "react"
import { Link, useParams } from "react-router-dom"
import { Api } from "../../Layouts/Template"
import { base_url } from "../../Function/Global"
import ContainerTitle from "../../Components/ContainerTitle"
import Index from "../../Function/Index"
import Form from "./Form"

const Update = () => {
    const api = useContext(Api)
    const className = useParams().className
    const act = new Index({ api, endpoint: "icon" })
    const [icon, setIcon] = useState()
    const [categories, setCategories] = useState()
    const [types, setTypes] = useState()
    const [dimensions, setDimensions] = useState()

    useEffect(() => {
        (async () => {
            const getIcon = await act.find(className)
            if (getIcon) setIcon(getIcon.data)
        })()

        api.get("category", res => setCategories(res.data))
        api.get("type", res => setTypes(res.data))
        api.get("dimension", res => setDimensions(res.data))
    }, [])

    const update = ({ target }) => {
        act.setEndpoint(`icon/${className}`)
        act.update({ target }, res => {
            if (res.ok) setIcon(res.data)
        })
    }

    return (
        <div className="content">
            <ContainerTitle labels={[
                { content: <Link to={base_url("icon")}>Data Icons</Link> },
                { content: "Update Icon", isActive: true }
            ]} />

            <Form categories={categories} types={types} dimensions={dimensions} send={update} icon={icon} isCreate={false} />
        </div>
    )
}

export default Update