import { memo } from "react"
import { setErrors, showPassword } from "../../Function/Global"
import Icons from "../../Components/Icons"
import Input from "../../Components/Input"

const Signup = ({ setIsSignin, api }) => {
    const formAttributes = [
        { type: "text", name: "name", placeholder: "Enter your name" },
        { type: "email", placeholder: "Enter your email address" },
        { type: "password", placeholder: "Enter your password", children: <Icons iconName="eye-crossed" onClick={showPassword} />, classList: "_input-password" }
    ]

    const register = ({ target }) => {
        target.setAttribute("disabled", "")

        api.post("register", target.form, res => {
            if (!res.ok) return setErrors(res.errors)
            return setIsSignin(state => !state)
        })
    }

    return (
        <>
            {formAttributes.map(({ type, name, placeholder, children, classList }, i) => <Input
                key={i}
                type={type}
                name={name}
                placeholder={placeholder}
                classList={classList}
            >{children}</Input>)}

            <div>
                <button className="button" onClick={register} type="button">Sign Up</button>
            </div>
        </>
    )
}

export default memo(Signup)