import { useContext, useEffect, useState } from "react"
import { Api } from "../../Layouts/Template"
import { getLargestId } from "../../Function/Global"
import IndexAction from "../../Function/Index"
import Index from "../../Components/Index"
import Table from "../../Components/Table"
import Icons from "../../Components/Icons"

const Type = () => {
    const api = useContext(Api)
    const [types, setTypes] = useState()
    const act = new IndexAction({ api, endpoint: "type" })
    const thead = ["No", "Type", "Actions"]

    useEffect(() => { act.get(res => setTypes(res.data)) }, [])

    const searchType = async evt => {
        const res = await act.search(evt, types.length)
        if (res) setTypes(res.data)
    }

    const getNextType = ({ target }) => {
        if (!types) return
        act.next({ target, lastId: getLargestId(types) }, res => {
            setTypes(types => types.concat(res.data))
        })
    }

    const deleteType = id => {
        act.delete({ id, take: types.length, errorMessage: "Gagal" }, res => setTypes(res.data))
    }

    return (
        <div className="content" onScroll={getNextType}>
            <Index label="Type" searchOnInput={searchType}>
                <Table thead={thead} classList={{ 2: "text-center" }}>
                    {types && types.map(({ type_name, id }, i) => (
                        <tr key={id}>
                            <td>{++i}</td>
                            <td>{type_name}</td>
                            <td className="text-center">
                                <Icons iconName="trash" onClick={() => deleteType(id)} />
                            </td>
                        </tr>
                    ))}
                </Table>
            </Index>
        </div>
    )
}

export default Type