import { memo } from "react"
import { Link } from "react-router-dom"

const NavigationList = ({ list = [] }) => {
    return (
        <ul>
            {list.map(({ to, name, icon, onClick }, i) =>
                <li key={i}>
                    <Link to={to} onClick={onClick} className={`navigation-link`}>
                        {icon}
                        <span className="sidebar-text">{name}</span>
                    </Link>
                </li>
            )}
        </ul>
    )
}

export default memo(NavigationList)