import { memo } from "react"

const Input = ({ type, name, placeholder, children, classList, bgColor, defaultValue }) => {
    const send = (evt) => {
        const { key, target } = evt
        if (key !== "Enter") return

        evt.preventDefault()
        const buttonSend = target.form.querySelector("button.send")
        if (buttonSend) buttonSend.click()
    }

    return (
        <div className={classList}>
            <input defaultValue={defaultValue} type={type} name={name ?? type} placeholder={placeholder} className={`input ${bgColor}`} onKeyDown={send} />
            <p className="invalid">Error</p>
            {children}
        </div>
    )
}

export default memo(Input)