const set = (local, data) => local ? localStorage.setItem("auth", data) : sessionStorage.setItem("auth", data)

export const setAuth = (data, isRemember) => {
    let storageData = {
        login: true,
        user: data.user,
        _GIT: data.token
    }

    storageData = JSON.stringify(storageData)
    return set(isRemember, storageData)
}

export const getAuth = () => {
    const auth = localStorage.getItem("auth") || sessionStorage.getItem("auth")
    return JSON.parse(auth)
}

export const updateToken = token => {
    let auth = localStorage.getItem("auth")
    let inLocal = true
    if (!auth) {
        auth = sessionStorage.getItem("auth")
        inLocal = !inLocal
    }

    auth = JSON.parse(auth)
    auth._GIT = token
    auth = JSON.stringify(auth)
    return set(inLocal, auth)
}