import { memo } from "react"
import { Link } from "react-router-dom"
import ContainerTitle from "./ContainerTitle"
import Icon from "./Icons"
import LoaderBuble from "./LoaderBuble"
import Search from "./Search"

const Index = ({ label, children, searchOnInput }) => {
    return (
        <>
            <ContainerTitle labels={[
                { isActive: true, content: `Data ${label}` },
                { content: <Link to="create">New {label}</Link> }
            ]} />

            <section className="index container content">
                <h1>{label}</h1>

                <div className="options">
                    <LoaderBuble />
                    <Search onInput={searchOnInput} />

                    <div className="filters">
                        <Icon iconName="settings-sliders" />
                        <span>Filters</span>
                    </div>
                </div>

                {children}

                <p className="empty-content hidden">Out Of Content</p>
                <LoaderBuble />
            </section>
        </>
    )
}

export default memo(Index)