import { memo } from "react"
import { renderImage, el } from "../../Function/Global"
import InputFile from "../../Components/InputFile"

const Form = ({ categories, types, dimensions, send, icon, isCreate = true }) => {
    const setFigure = (image, target) => {
        renderImage(image, res => {
            const figure = el(`#${target}`)

            // create element image
            const img = document.createElement("img")
            img.setAttribute("src", res)
            img.setAttribute("alt", image.name)

            // remove figcaption and append img in figure
            figure.removeChild(figure.firstElementChild)
            figure.appendChild(img)
        })
    }

    if (icon) {
        const free = el(`[name="is_free"] [value="${icon.is_free}"]`)
        if (free) free.setAttribute("selected", "")
    }

    return (
        <form className="create-icon">
            <section className="content container">
                <h4 className="container-title">New Icon</h4>

                <div className="images">
                    <figure id="picture">
                        {icon ? (<img src={icon.picture} alt={icon.class_name} />) : (<figcaption>Icon Picture</figcaption>)}
                    </figure>
                    <figure id="svg">
                        <figcaption>Icon Svg</figcaption>
                    </figure>
                </div>
                <InputFile label="Icon Picture" accept=".png" name="picture" afterInputCallback={image => setFigure(image, "picture")} />
                <InputFile label="Icon Svg" name="svg" accept=".svg" afterInputCallback={image => setFigure(image, "svg")} />
            </section>

            <section className="content container form-crud">
                {isCreate ? (
                    <div>
                        <label>Icon Class Name</label>
                        <input type="text" className="input bg-grey" name="class_name" placeholder="Class Name Icon"
                            defaultValue={icon ? icon.class_name : ""} />
                        <p className="invalid">Error</p>
                    </div>
                ) : ""}
                <div>
                    <label>Icon Free</label>
                    <select name="is_free" className="input bg-grey">
                        <option value="1">Yes</option>
                        <option value="0">No</option>
                    </select>
                    <p className="invalid">Error</p>
                </div>
                <div>
                    <label>Icon Category</label>
                    <select name="category_id" className="input bg-grey">
                        {categories && categories.map(({ category_name, id }) => (
                            <option key={id} value={id}>{category_name}</option>
                        ))}
                    </select>
                    <p className="invalid">Error</p>
                </div>
                <div>
                    <label>Icon Type</label>
                    <select name="type_id" className="input bg-grey">
                        {types && types.map(({ type_name, id }) => (
                            <option key={id} value={id}>{type_name}</option>
                        ))}
                    </select>
                    <p className="invalid">Error</p>
                </div>
                <div>
                    <label>Icon Dimension</label>
                    <select name="dimension_id" className="input bg-grey">
                        {dimensions && dimensions.map(({ dimension, id }) => (
                            <option key={id} value={id}>{dimension}</option>
                        ))}
                    </select>
                    <p className="invalid">Error</p>
                </div>
                <div>
                    <button className="button" type="button" onClick={send}>Save</button>
                </div>
            </section>
        </form>
    )
}

export default memo(Form)