import { useContext } from "react"
import { Link } from "react-router-dom"
import { Api } from "../../Layouts/Template"
import { base_url } from "../../Function/Global"
import Input from "../../Components/Input"
import ContainerTitle from "../../Components/ContainerTitle"
import Index from "../../Function/Index"

const Create = () => {
    const api = useContext(Api)
    const act = new Index({ api, endpoint: "dimension" })

    const save = ({ target }) => {
        act.save({ target })
    }

    return (
        <div className="content">
            <ContainerTitle labels={[
                { content: <Link to={base_url("dimension")}>Data Dimension</Link> },
                { isActive: true, content: "New Dimension" }
            ]} />

            <div className="content container">
                <form className="form-crud">
                    <Input
                        name="dimension"
                        type="text"
                        bgColor="bg-grey"
                        placeholder="Dimension Size"
                        children={(
                            <label>Dimension Size <sup>*</sup></label>
                        )} />
                    <button type="button" className="button send" onClick={save}>Add New Dimension</button>
                </form>
            </div>
        </div>
    )
}

export default Create