import { memo } from "react"
import Icon from "../../Components/Icons"
import Card from "../../Components/Card"

const StatisticsCard = ({ icon, user, category, type }) => {
    const statisticsCard = [
        { icon: <Icon iconName="rocket-launch" />, name: "Total Icon", count: icon && icon.length },
        { icon: <Icon iconName="users" />, name: "Total User", count: user && user.length },
        { icon: <Icon iconName="document-signed" />, name: "Category Icon", count: category && category.length },
        { icon: <Icon iconName="list" />, name: "Type Icon", count: type && type.length }
    ]

    return (
        <div className="statistics-card">
            {statisticsCard.map(({ icon, name, count }, id) => <Card
                key={id}
                icon={icon}
                name={name}
                count={count}
            />
            )}
        </div>
    )
}

export default memo(StatisticsCard)