import { useContext, useEffect, useState } from "react"
import { Api } from "../../Layouts/Template"
import { getSmalltId } from "../../Function/Global"
import IndexAction from "../../Function/Index"
import Index from "../../Components/Index"
import Table from "../../Components/Table"
import Icons from "../../Components/Icons"

const Category = () => {
    const api = useContext(Api)
    const [categories, setCategories] = useState()
    const act = new IndexAction({ api, endpoint: "category" })
    const thead = ["No", "Category", "Action"]

    useEffect(() => { act.get(res => setCategories(res.data)) }, [])

    const searchCategory = async evt => {
        const res = await act.search(evt, categories.length)
        if (res) setCategories(res.data)
    }

    const getNextCategory = ({ target }) => {
        if (!categories) return
        act.next({ lastId: getSmalltId(categories), target, direction: "before" }, res => {
            setCategories(categories => categories.concat(res.data))
        })
    }

    const deleteCategory = id => {
        act.delete({
            id,
            take: categories.length,
            errorMessage: "Failed to delete, maybe the category is being used"
        }, res => setCategories(res.data))
    }

    return (
        <div className="content" onScroll={getNextCategory}>
            <Index label={"Category"} searchOnInput={searchCategory}>
                <Table thead={thead} classList={{ 2: "text-center" }}>
                    {categories && categories.map(({ category_name, id }, i) => (
                        <tr key={i}>
                            <td>{++i}</td>
                            <td>{category_name}</td>
                            <td className="text-center">
                                <Icons iconName="trash" onClick={() => deleteCategory(id)} />
                            </td>
                        </tr>
                    ))}
                </Table>
            </Index>
        </div>
    )
}

export default Category