function Api(url, { token }) {
    this.url = url
    this.token = token
}

Api.prototype.get = function (path, response) {
    fetch(`${this.url}/${path}`, {
        headers: {
            Authorization: `Bearer ${this.token}`
        }
    })
        .then(r => r.json())
        .then(r => response(r))
}

Api.prototype.getPromises = function (path) {
    return new Promise(resolve => {
        fetch(`${this.url}/${path}`, {
            headers: {
                Authorization: `Bearer ${this.token}`
            }
        })
            .then(r => r.json())
            .then(r => resolve(r))
    })
}

Api.prototype.post = function (path, form, response) {
    fetch(`${this.url}/${path}`, {
        method: "POST",
        body: new FormData(form),
        headers: {
            Authorization: `Bearer ${this.token}`
        }
    })
        .then(r => r.json())
        .then(r => response(r))
}

Api.prototype.put = function (path, form, response) {
    const body = new FormData(form)
    body.append("_method", "PUT")
    body.forEach((f, k) => !f ? body.delete(`${k}`) : null)

    fetch(`${this.url}/${path}`, {
        method: "POST",
        body,
        headers: {
            Authorization: `Bearer ${this.token}`
        }
    })
        .then(r => r.json())
        .then(r => response(r))
}

Api.prototype.delete = function (path, response) {
    fetch(`${this.url}/${path}`, {
        method: "DELETE",
        headers: {
            Authorization: `Bearer ${this.token}`
        }
    })
        .then(r => r.json())
        .then(r => response(r))
}

let api
const FetchApi = (url, { token }) => {
    if (!api) api = new Api(url, { token })
    if (api.url !== url || api.token !== token) api = new Api(url, { token })

    return api
}

export default FetchApi