import { useContext, useEffect, useState } from "react"
import { Api } from "../../Layouts/Template"
import { stringToDate, getLargestId } from "../../Function/Global"
import IndexAction from "../../Function/Index"
import LoaderBuble from "../../Components/LoaderBuble"
import Search from "../../Components/Search"
import Icons from "../../Components/Icons"
import Table from "../../Components/Table"
import ContainerTitle from "../../Components/ContainerTitle"

const User = () => {
    const api = useContext(Api)
    const [users, setUsers] = useState()
    const act = new IndexAction({ api, endpoint: "user" })
    const thead = ["No", "Nama", "Email", "Tanggal Terdaftar"]

    const getDate = dateObj => `${dateObj.date} - ${dateObj.month} - ${dateObj.year}`

    useEffect(() => { act.get(res => setUsers(res.data)) }, [])
    const searchUser = async evt => {
        const res = await act.search(evt, users.length)
        if (res) setUsers(res.data)
    }

    const getNextUser = ({ target }) => {
        if (!users) return
        act.next({ target, lastId: getLargestId(users) }, res => {
            setUsers(users => users.concat(res.data))
        })
    }

    return (
        <div className="content" onScroll={getNextUser}>
            <ContainerTitle labels={[
                { isActive: true, content: `Data User` }
            ]} />

            <section className="index container content">
                <h1>Users</h1>

                <div className="options">
                    <LoaderBuble />
                    <Search onInput={searchUser} />

                    <div className="filters">
                        <Icons iconName="settings-sliders" />
                        <span>Filters</span>
                    </div>
                </div>

                <Table thead={thead} classList={{ 3: "text-center" }}>
                    {!users ?
                        <tr>
                            <td>1</td>
                            <td>Loading</td>
                            <td>Bro</td>
                            <td>Santuy...</td>
                        </tr>
                        :
                        users.map(({ id, name, email, user_create }, i) => (
                            <tr key={id}>
                                <td>{++i}</td>
                                <td>{name}</td>
                                <td>{email}</td>
                                <td className="text-center">{getDate(stringToDate(user_create))}</td>
                            </tr>
                        ))
                    }
                </Table>

                <p className="empty-content hidden">Out Of Content</p>
                <LoaderBuble />
            </section>
        </div>
    )
}

export default User