import { useContext, useEffect, useState } from "react"
import { Api } from "../../Layouts/Template"
import StatisticsCard from "./StatisticsCard"
import StatisticsGraph from "./StatisticsGraph"
import Statistics from "./Statistics"
import "chart.js/auto"

const Dashboard = () => {
    const api = useContext(Api)
    const [icon, setIcon] = useState()
    const [category, setCategory] = useState()
    const [type, setType] = useState()
    const [user, setUser] = useState()
    const [popularIcons, setPopularIcons] = useState()
    const [subscriber, setSubscriber] = useState()

    useEffect(() => {
        api.get("icon", res => setIcon(res.data))
        api.get("category", res => setCategory(res.data))
        api.get("type", res => setType(res.data))
        api.get("user", res => setUser(res.data))
        api.get("popular/icon?take=6", res => setPopularIcons(res.data))
        api.get(`subscriber`, res => setSubscriber(res.data))

        const getData = setInterval(() => {
            api.get("user", res => setUser(res.data))
            api.get("popular/icon?take=6", res => setPopularIcons(res.data))
            api.get(`subscriber`, res => setSubscriber(res.data))
        }, 1000 * 30) // 30 detik

        return () => clearInterval(getData)
    }, [])

    return (
        <div className="dashboard content">
            <StatisticsCard user={user} type={type} category={category} icon={icon} />
            <StatisticsGraph popularIcons={popularIcons} />
            <Statistics category={category} user={user} subscriber={subscriber} />
        </div>
    )
}

export default Dashboard