import { actived, base_url, el, logout } from "../Function/Global"
import Search from "../Components/Search"
import NavigationList from "../Components/NavigationList"
import Icons from "../Components/Icons"

const Header = () => {
    const openCollapse = () => {
        el(".user-action").classList.toggle("collapse")
    }

    const userActions = [
        { to: base_url("profile"), name: "Profile", icon: <Icons iconName="user" /> },
        { to: base_url("setting"), name: "Setting", icon: <Icons iconName="settings" /> },
        { to: base_url(), name: "Signout", icon: <Icons iconName="box-arrow-right" />, onClick: logout }
    ]

    return (
        <header>
            <Search />

            <div className="user">
                <div className="_icon" onClick={openCollapse}>
                    <Icons iconName="search" />
                </div>

                <div className="user-action" onClick={actived}>
                    <NavigationList list={userActions} />
                </div>
            </div>
        </header>
    )
}

export default Header