import { useContext, useEffect, useState } from "react"
import { Link } from "react-router-dom"
import { Api } from "../../Layouts/Template"
import { base_url } from "../../Function/Global"
import IndexAction from "../../Function/Index"
import Index from "../../Components/Index"
import Icons from "../../Components/Icons"
import Table from "../../Components/Table"

const Icon = () => {
    const api = useContext(Api)
    const [icons, setIcons] = useState()
    const act = new IndexAction({ api, endpoint: "icon" })
    const thead = ["No", "Class Name", "Image", "Action"]

    useEffect(() => { act.get(res => setIcons(res.data)) }, [])
    const searchIcon = async evt => {
        const res = await act.search(evt, icons.length)
        if (res) setIcons(res.data)
    }

    const getNextIcon = ({ target }) => {
        if (!icons) return
        const lastName = icons[icons.length - 1].class_name
        act.next({ target, lastId: lastName }, res => {
            setIcons(icons => icons.concat(res.data))
        })
    }

    const deleteIcon = class_name => {
        act.delete({ id: class_name, take: icons.length, errorMessage: "Failed to delete" }, res => setIcons(res.data))
    }

    return (
        <div className="content" onScroll={getNextIcon}>
            <Index label="Icons" searchOnInput={searchIcon}>
                <Table thead={thead} classList={{ 2: "text-center", 3: "text-center" }}>
                    {icons && icons.map(({ class_name, picture }, i) => (
                        <tr key={i}>
                            <td>{++i}</td>
                            <td>{class_name}</td>
                            <td className="text-center">
                                <img src={picture} alt={class_name} />
                            </td>
                            <td className="text-center action">
                                <Link to={base_url(`icon/${class_name}/edit`)}>
                                    <Icons iconName="pencil" />
                                </Link>
                                <Link to={base_url(`icon/${class_name}`)}>
                                    <Icons iconName="eye-fill" />
                                </Link>
                                <Icons iconName="trash" onClick={() => deleteIcon(class_name)} />
                            </td>
                        </tr>
                    ))}
                </Table>
            </Index>
        </div >
    )
}

export default Icon