export const el = element => document.querySelector(`${element}`)

// production url
export const base_url = path => `/get-icon-be${path ? `/${path}` : ""}`

// development url
// export const base_url = path => `/`

export const showPassword = ({ currentTarget }) => {
    const input = currentTarget.parentElement.querySelector("input")
    const setAttributeInput = type => type === "text" ? "password" : "text"
    if (input) input.setAttribute("type", setAttributeInput(input.getAttribute("type")))
}

export const logout = () => localStorage.clear() || sessionStorage.clear()

export const actived = () => {
    const activeElement = el(`.sidebar [href="${window.location.pathname}"]`)
    if (!activeElement) return

    const activeNow = el(".sidebar .active")
    if (activeNow) activeNow.classList.remove("active")

    activeElement.classList.add("active")
}

export const stringToDate = str => {
    const date = new Date(str * 1000)
    const months = ["Jan", "Feb", "Mar", "Apr", "Mei", "Jun", "Jul", "Agu", "Sep", "Okt", "Nov", "Des"]

    return {
        year: date.getFullYear(),
        month: months[date.getMonth()],
        date: date.getDate()
    }
}

export const setErrors = errors => {
    for (const error in errors) {
        const input = el(`[name="${error}"]`)
        if (input) {
            input.classList.add("is-invalid")
            input.nextElementSibling.textContent = errors[error]
            input.addEventListener("input", function () {
                this.classList.remove("is-invalid")
            })
        }
    }
}

export const renderImage = (image, onRender) => {
    const reader = new FileReader()
    reader.readAsDataURL(image)
    reader.onload = ({ target }) => onRender(target.result)
}

export const getLargestId = arr => {
    let large = 0
    arr.map(value => (value.id > large) ? large = value.id : null)
    return large
}

export const getSmalltId = arr => {
    let small = arr[0].id
    arr.map(value => (value.id < small) ? small = value.id : null)
    return small
}