import { memo, useContext, useEffect, useState } from "react"
import { Api } from "../../Layouts/Template"
import { el, renderImage } from "../../Function/Global"
import Input from "../../Components/Input"
import InputFile from "../../Components/InputFile"
import Index from "../../Function/Index"

const Setting = () => {
    const api = useContext(Api)
    const act = new Index({ api, endpoint: "company" })
    const [company, setCompany] = useState()

    useEffect(() => { act.get(res => setCompany(res.data)) }, [])
    const updateCompany = ({ target }) => { act.update({ target }) }

    const inputs = [
        { type: "text", name: `name`, devaultValue: company && company.name, label: <label>Name <sup>*</sup></label> },
        { type: "text", name: "behance", devaultValue: company && company.behance, label: <label>Behance <sup>*</sup></label> },
        { type: "text", name: "github", devaultValue: company && company.github, label: <label>Github <sup>*</sup></label> },
        { type: "text", name: "description", devaultValue: company && company.description, label: <label>Description <sup>*</sup></label> }
    ]

    const setLogo = logo => renderImage(logo, result => el("#img").setAttribute("src", result))

    return (
        <form className="content setting">
            {company && <>
                <div className="container content">
                    <h2>Logo</h2>
                    <figure className="logo">
                        <img id="img" src={company.logo} alt={company.logo} />
                        <figcaption>logo.png</figcaption>
                    </figure>
                    <InputFile name="logo" label="Upload Icon" accept=".png" afterInputCallback={file => setLogo(file)} />
                </div>

                <div className="container content form-crud">
                    {inputs.map(({ type, name, devaultValue, label }, i) => (
                        <Input key={i} type={type} name={name} defaultValue={devaultValue} children={label} bgColor="bg-grey" />
                    ))}
                    <button type="button" className="button" onClick={updateCompany}>Update</button>
                </div>
            </>}
        </form>
    )
}

export default memo(Setting)