import { Link } from "react-router-dom"
import { useContext } from "react"
import { Api } from "../../Layouts/Template"
import { base_url } from "../../Function/Global"
import Input from "../../Components/Input"
import ContainerTitle from "../../Components/ContainerTitle"
import Index from "../../Function/Index"

const Create = () => {
    const api = useContext(Api)
    const act = new Index({ api, endpoint: "type" })
    const save = ({ target }) => {
        act.save({ target })
    }

    return (
        <div className="content">
            <ContainerTitle labels={[
                { content: <Link to={base_url("type")}>Data Type</Link> },
                { isActive: true, content: "New Type" }
            ]} />

            <div className="content container">
                <form className="form-crud">
                    <Input
                        name="type_name"
                        type="text"
                        bgColor="bg-grey"
                        placeholder="Type Name"
                        children={(
                            <label>Type Name <sup>*</sup></label>
                        )} />
                    <button type="button" className="button send" onClick={save}>Add New Type</button>
                </form>
            </div>
        </div>
    )
}

export default Create