import { useState } from "react"
import Signin from "./Signin"
import SignUp from "./Signup"
import FetchApi from "../../Api/FetchApi"
import { base_url } from "../../Function/Global"

const LoginRegister = () => {
    const [isSignin, setIsSignin] = useState(true)
    const api = FetchApi(`https://get.icon.giftin-id.com/api`, { token: null }) // jangan gunakan garis miring "/" du akhir
    const title = ["Sign in", "Sign Up"]
    if (!isSignin) title.reverse()

    return (
        <div className="signin-signout">
            <div className="introduction">
                <h1>Welcome to our <br />
                    Comunity
                </h1>
                <h5>A whole new productive journey <br />
                    start right here
                </h5>
                <img src={base_url("assets/images/people-working.png")} alt="people working" />
            </div>

            <div className="login-register-form">
                <div className="login-register-title">
                    <h4>{title[0]}</h4>
                    <button onClick={() => setIsSignin(state => !state)}>{title[1]}</button>
                </div>

                <form>
                    {isSignin ? <Signin api={api} /> : <SignUp api={api} setIsSignin={setIsSignin} />}
                </form>
            </div>
        </div>
    )
}

export default LoginRegister