import { memo } from "react"

const InputFile = ({ name, label, accept, afterInputCallback }) => {
    const openFile = ({ target }) => {
        target.parentElement.querySelector(`input[name="${name}"]`).click()
    }

    const inputed = ({ target }) => {
        const file = target.files[0]
        target.parentElement.querySelector("label").textContent = file.name

        if (typeof afterInputCallback === "function") afterInputCallback(file)
    }

    return (
        <div className="input-file">
            <label>{label}</label>
            <input type="file" className="hidden" name={name} onInput={inputed} accept={accept} />
            <p className="invalid">Error</p>
            <button type="button" onClick={openFile}>Pilih File</button>
        </div>
    )
}

export default memo(InputFile)