import { useContext, useEffect, useState } from "react"
import { Link, useParams } from "react-router-dom"
import { Api } from "../../Layouts/Template"
import { base_url } from "../../Function/Global"
import Index from "../../Function/Index"
import ContainerTitle from "../../Components/ContainerTitle"
import Loading from "../../Components/Loading"

const Detail = () => {
    const api = useContext(Api)
    const act = new Index({ api, endpoint: "icon" })
    const className = useParams().className
    const [icon, setIcon] = useState()

    useEffect(() => {
        (async function () {
            const findIcon = await act.find(className)
            console.log(findIcon)
            if (findIcon) setIcon(findIcon.data)
        })()
    }, [])

    return (
        <div className="content">
            <ContainerTitle labels={[
                { content: <Link to={base_url("icon")}>Data Icons</Link> },
                { content: "Detail Icon", isActive: true }
            ]} />

            <div className="detail">
                {
                    !icon ?
                        <Loading animated="animated-duration-2s" /> :
                        <>
                            <section className="content container">
                                <h4 className="container-title">Detail Icon</h4>

                                <figure>
                                    <img src={icon.picture} alt={icon.class_name} />
                                    <figcaption>{`${icon.class_name}.png`}</figcaption>
                                </figure>
                            </section>

                            <section className="content container form-crud">
                                <div>
                                    <label>Icon Class Name</label>
                                    <input type="text" value={icon.class_name} disabled className="input bg-grey" />
                                </div>
                                <div>
                                    <label>Icon Free</label>
                                    <input type="text" value={icon.is_free ? "Yes" : "No"} disabled className="input bg-grey" />
                                </div>
                                <div>
                                    <label>Icon Downloaded</label>
                                    <input type="text" value={`${icon.counter} - Download`} disabled className="input bg-grey" />
                                </div>
                                <div>
                                    <label>Icon Category</label>
                                    <input type="text" value={`${icon.category.category_name}`} disabled className="input bg-grey" />
                                </div>
                                <div>
                                    <label>Icon Type</label>
                                    <input type="text" value={`${icon.type.type_name}`} disabled className="input bg-grey" />
                                </div>
                                <div>
                                    <label>Icon Dimension</label>
                                    <input type="text" value={`${icon.dimension.dimension}`} disabled className="input bg-grey" />
                                </div>
                            </section>
                        </>
                }
            </div>
        </div>
    )
}

export default Detail