import { el, setErrors } from "./Global"
import Swal from "sweetalert2"

export const Toast = Swal.mixin({
    toast: true,
    position: 'top-end',
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
    }
})

export default function Index({ api, endpoint }) {
    this.api = api
    this.endpoint = endpoint
    this.isRunning = false
    this.empty = el(".empty-content")
    this.loader = {
        index: el(".index>.loader-buble"),
        option: el(".options>.loader-buble")
    }
    this.searchInput = el(".index .search input")
}

// Endpoint
Index.prototype.setEndpoint = function (endpoint) {
    this.endpoint = endpoint
}

// Loader
Index.prototype.showLoader = function (type) {
    let loader = this.loader
    if (type === "index") loader = loader.index
    if (type === "option") loader = loader.option
    loader.classList.remove("hidden")
}

Index.prototype.hiddenLoader = function (type) {
    let loader = this.loader
    if (type === "index") loader = loader.index
    if (type === "option") loader = loader.option
    loader.classList.add("hidden")
}

// Empty Content
Index.prototype.hiddenEmpty = function () {
    this.empty.classList.add("hidden")
}

Index.prototype.showEmpty = function () {
    this.empty.classList.remove("hidden")
}

// Other
Index.prototype.get = function (callback) {
    if (this.isRunning) return
    this.isRunning = true
    this.api.get(`${this.endpoint}?take=5`, res => {
        callback(res)
        this.isRunning = false
    })
}

Index.prototype.find = async function (id) {
    if (this.isRunning) return
    this.isRunning = true

    const result = await this.api.getPromises(`${this.endpoint}/${id}`)
    this.isRunning = false

    return result
}

Index.prototype.search = async function (evt, take) {
    if (evt.key !== "Enter") return
    if (take < 5) take = 5

    this.showLoader("option")

    const result = await this.api.getPromises(`${this.endpoint}?take=${take}&s=${evt.target.value}`)
    this.hiddenLoader("option")
    this.hiddenEmpty()

    return result
}

Index.prototype.next = function ({ lastId, target, direction = "after" }, callback) {
    if (!this.empty) return
    const isAvailableContent = this.empty.classList.contains("hidden")
    if (target.scrollTop !== target.scrollTopMax || this.isRunning || !isAvailableContent) return

    this.showLoader("index")
    this.isRunning = true
    this.api.get(`${this.endpoint}?take=5&s=${this.searchInput.value}&${direction}=${lastId}`, res => {
        callback(res)
        this.isRunning = false
        this.hiddenLoader("index")
        if (res.data.length === 0) this.showEmpty()
    })
}

Index.prototype.save = function ({ target }) {
    target.setAttribute("disabled", "")

    this.api.post(this.endpoint, target.form, res => {
        if (!res.ok) setErrors(res.errors)
        if (res.ok) {
            Toast.fire({
                icon: "success",
                title: `Successfully added ${this.endpoint}`
            })
        }

        target.removeAttribute("disabled")
    })
}

Index.prototype.update = function ({ target }, callback) {
    target.setAttribute("disabled", "")

    this.api.put(this.endpoint, target.form, res => {
        if (!res.ok) setErrors(res.errors)
        if (res.ok) {
            Toast.fire({
                icon: "success",
                title: `Updated successfuly`
            })
        }
        if (callback) callback(res)
        target.removeAttribute("disabled")
    })
}

Index.prototype.delete = function ({ id, take, errorMessage }, callback) {
    Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!"
    }).then((result) => {
        if (!result.isConfirmed) return

        this.api.delete(`${this.endpoint}/${id}`, async res => {
            if (res.ok) {
                const result = await this.api.getPromises(`${this.endpoint}?s=${this.searchInput.value}&take=${take}`)
                callback(result)
                Toast.fire({
                    icon: "success",
                    title: "Delete successfully"
                })
            } else {
                Toast.fire({
                    icon: "error",
                    title: errorMessage ?? "Failed to delete"
                })
            }
        })
    })
}