import { memo } from "react"
import { Line } from "react-chartjs-2"
import Loading from "../../Components/Loading"

const StatisticsGraph = ({ popularIcons }) => {
    const labels = []
    const counters = []
    if (popularIcons) {
        popularIcons.map(icon => {
            labels.push(icon.class_name)
            counters.push(icon.counter)
        })
    }

    return (
        <div className="container statistics-graph">
            {!popularIcons ? <Loading animated="animated-duration-3s" /> :
                <Line data={{
                    labels,
                    datasets: [{
                        label: "Popular Icons",
                        data: counters,
                        fill: false,
                        borderColor: "#0182ff",
                        hoverBorderColor: "#ef3f3f",
                        tension: 0.4,
                        cubicInterpolationMode: "monotone",
                        backgroundColor: "#0182ff",
                        borderWidth: 2
                    }]
                }} />
            }
        </div>
    )
}

export default memo(StatisticsGraph)