import { memo } from "react"
import { Doughnut } from "react-chartjs-2"
import Loading from "../../Components/Loading"

const Statistics = ({ category, user, subscriber }) => {
    return (
        <div className="statistics container">
            <section>
                <h2>Statistics</h2>

                <div className="statistics-doughnut">
                    {
                        (user && subscriber) ? <Doughnut data={
                            {
                                labels: ["Users", "Subscribers"],
                                datasets: [{
                                    data: [user.length - subscriber.length, subscriber.length],
                                    backgroundColor: [
                                        "#464646",
                                        "#0182ff"
                                    ],
                                    hoverOffset: 4
                                }]
                            }
                        } /> : <Loading animated="animated-duration-4s" />
                    }
                </div>
            </section>
            <section>
                <h2>New Category</h2>

                <ul className="new-category">
                    {!category ? <Loading animated="animated-duration-4s" /> :
                        category.map(({ id, category_name }, i) => (
                            (i < 6) && <li key={id}>{category_name}</li>
                        ))
                    }
                </ul>
            </section>
        </div>
    )
}

export default memo(Statistics)