import { memo } from "react"

const Table = ({ thead, children, classList = {} }) => {
    return (
        <table>
            <thead>
                <tr>
                    {thead.map((val, i) => (<th key={i} className={classList[i]}>{val}</th>))}
                </tr>
            </thead>
            <tbody>
                {children}
            </tbody>
        </table>
    )
}

export default memo(Table)